.card {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: $size-template;
  border-radius: 5px;
  border: 1px solid #f3f3f3;
  padding: 32px 16px 16px;
  transition: all $time-activ;

  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 3px 40px;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 150%;
    margin: 0 0 4px;
    color: $titleColor;
  }

  &:hover &__title {
    transition: color $time-activ;
    color: $text-color-hover;
  }

  &__product-code {
    color: $productColor;
    font-size: 10px;
  }

  &__reviews {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 17px;

    &-text {
      font-size: 10px;
      line-height: 14px;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;
  }

  &__price-label {
    color: $productColor;
    font-size: 12px;
    line-height: 18px;
  }

  &__price-amount {
    color: $titleColor;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 16px;
    height: 40px;
    background-color: $color-blue;
    color: $main-color;
    text-decoration: none;
    border-radius: 5px;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;

    &:hover {
      border: 1px solid $color-blue;
      background-color: $main-color;
      color: $text-color-hover;
      text-transform: uppercase;
      font-weight: 700;
    }
  }
}

.card:hover {
  transform: scale(1.2);
  transition: $time-activ;
}
